html, body {
  height: 100%;
  width: 100%;
  margin: auto;
  font-family: Varela Round, sans-serif;
}

.content {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 32px;
  display: flex;
}

.header {
  text-transform: uppercase;
  font-size: 48px;
}

.display-scores {
  text-align: center;
  width: 120px;
  height: 20px;
  text-transform: uppercase;
  color: #f5f5f5;
  background-color: #6aaa64;
  border-radius: 3px;
  justify-content: center;
  margin-top: 12px;
  padding: 10px 20px;
  font-size: 16px;
  display: flex;
  position: relative;
}

.display-header {
  padding-right: 5px;
}

.grid {
  display: grid;
}

#grid {
  grid-template-rows: repeat(8, 64px);
  grid-template-columns: repeat(5, 64px);
  margin: 24px;
}

#word-grid {
  grid-template-rows: repeat(1, 48px);
  grid-template-columns: repeat(5, 48px);
  margin: 16px;
}

.tile {
  text-transform: uppercase;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  margin: 4px;
  font-size: 36px;
  display: flex;
}

.tile[data-type="empty"] {
  background-color: #0000;
  border: 2px solid #d3d6da;
}

.tile[data-type="full"] {
  color: #000;
  background-color: #0000;
  border: 2px solid #878a8c;
}

.tile[data-type="right"] {
  color: #fff;
  background-color: #6aaa64;
  border: none;
}

.tile[data-type="right-letter"] {
  color: #fff;
  background-color: #c9b458;
  border: none;
}

.tile[data-type="wrong"] {
  color: #fff;
  background-color: #787c7e;
  border: none;
}

.tile[data-size="small"] {
  border-radius: 4px;
  margin: 2px;
  font-size: 24px;
}

#end-message {
  text-align: center;
  margin: 4px;
  font-size: 32px;
}

#end-message[data-result="win"] {
  color: #6aaa64;
}

#end-message[data-result="lose"] {
  color: #787c7e;
}

#word-reveal {
  margin: 4px;
  font-size: 16px;
}

.icon-group {
  justify-content: center;
  display: flex;
}

.icon {
  fill: #787c7e;
}

.icon:hover {
  fill: #000;
}

